.smart-carousel button.arrow-prev,
.smart-carousel button.arrow-next {
  position: absolute;
  top: 50%;
  display: block;
  z-index: 1;
  width: 68px;
  height: 68px;
  margin-top: -69.5px;
  background: none;
  border: none;
  opacity: 0.5;
  -webkit-appearance: button;
  cursor: pointer;
}
.smart-carousel button.arrow-next {
  right: 0;
}
.smart-carousel button.arrow-prev {
  left: 0;
}
.smart-carousel .icon-arrow-right,
.smart-carousel .icon-arrow-left {
  display: block;
  height: 100%;
  background: url(data:image/svg+xml;base64,) no-repeat;
}
.smart-carousel .icon-arrow-right {
  transform: scaleX(-1);
}
.smart-carousel .smart-carousel-dots {
  position: absolute;
  width: 100%;
  height: auto;
  bottom: 30px;
  line-height: 0;
  text-align: center;
  opacity: 1;
  transition: opacity 0.5s ease-out;
  cursor: default;
}
.smart-carousel .carousel-dot {
  display: inline-block;
  width: 9px;
  height: 9px;
  margin: 0 5px;
  vertical-align: middle;
  background: rgba(118, 118, 118, 0.5);
  border: none;
  border-radius: 24px;
  cursor: pointer;
  outline: none;
}
.smart-carousel .active-dot {
  width: 11px;
  height: 11px;
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid #bababa;
}
.smart-carousel {
  display: flex;
  position: relative;
  overflow: hidden;
  justify-content: center;
}
.smart-carousel [data-slides-area] {
  display: flex;
  position: relative;
  overflow: hidden;
  padding-bottom: 71px;
  width: 780px;
}
.smart-carousel [data-slide-item] {
  position: relative;
  left: 0;
  box-sizing: border-box;
  min-width: 250px;
  min-height: 150px;
  margin: 0 5px;
  z-index: 1;
  background-color: aliceblue;
  transition: transform 1s linear;
}
.smart-carousel .active-slide {
  display: block;
  position: relative;
  visibility: visible;
  z-index: 2;
  opacity: 1;
}
.smart-carousel [data-slide-item] {
  display: none;
}
.smart-carousel .active-slide,
.smart-carousel .left,
.smart-carousel .right {
  display: block;
}
.smart-carousel .prev {
  position: absolute;
}
.smart-carousel .left {
  animation: move-from-left 1s linear;
}
.smart-carousel .left.prev {
  animation: move-form-active-to-left 1s linear;
}
.smart-carousel .right {
  animation: move-from-right 1s linear;
}
.smart-carousel .right.prev {
  animation: move-form-active-to-right 1s linear;
}
@keyframes move-from-left {
  from {
    left: -100%;
  }
  to {
    left: 0;
  }
}
@keyframes move-form-active-to-left {
  from {
    left: 0;
  }
  to {
    left: 100%;
  }
}
@keyframes move-from-right {
  from {
    left: 100%;
  }
  to {
    left: 0;
  }
}
@keyframes move-form-active-to-right {
  from {
    left: 0;
  }
  to {
    left: -100%;
  }
}
.smart-carousel[strategy="multi"] [data-slide-item] {
  display: block;
  transition: transform 1s linear;
}
